.Ambika{
    display: flex;
    justify-content:flex-start;
    margin-left: auto;
    margin-right: auto;
    max-width: 1600px;
}

.Ambika_row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.Ambika_right{
    display: flex;
    flex-direction: row;
    margin: 10px;
    padding: 20px;
    width: 30%;
    height: 100%;
    max-width: 100%;
    background-color: white;
    z-index: 1;
}