.product{
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding: 20px;
    width: 100%;
    height: 350px;
    max-width: 100%;
    background-color: white;
    z-index: 1;
}


.image {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}


.button{
    display: flex;
    background: orange;
    text-align: center;
    height: 25px;
    border: 1px solid;
    margin: auto;
    margin-top: 10px;
    border-color: black;
    color: #111;

}

.product__info{
    height: 100px;
    margin-bottom: 15px;
    
}

.product__price{
    margin-top: 5px;
}

.product__rating{
    margin-bottom: 10px;
    height: 30px;
    display: flex;
}