.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 100;
}


.header_logo {
    width: 130px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header_searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: orange;
}


.header_searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}

.header_optionCart{
    display: flex;
    align-items: center;
    color: white;
    margin-left: 10px;
}

.header_cartCount{
    margin-right: 10px;
}

.header_optionLineOne{
    font-size: 10px;
}

.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}